

.title-form {
  font-weight: 600;
}


.file-transfer {
  /* background-color: #625BFE; */
  width: 100%;
  min-height: 68vh;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.file-transfer .container-form {
  /* max-width: 500px; */
  width: 100%;
  background-color: #fff;
  padding: 30px;
  border-radius: 16px;
  /* box-shadow: #000000 0px 4px 12px; */
}

.file-transfer .title-section {
  margin-bottom: 30px;
}

.file-transfer .title-section .title-form {
  color: #6676f7;
  font-size: 25px;
  font-weight: 500;
  margin-top: 10px;
  text-transform: capitalize;
  text-align: center;
}


.file-transfer .showfilebox {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: #0000000d 0px 0px 0px 1px, #d1d5db3d 0px 0px 0px 1px inset;
  padding: 5px;
  border-radius: 5px;
  margin-bottom: 8px;
}

.file-transfer .showfilebox .left {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.file-transfer .showfilebox .left .filetype {
  background: #5a90d442;
  color: var(--green);
  font-size: 15px;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 3px;
}

.file-transfer .showfilebox .left h3 {
  font-size: 15px;
  font-weight: 500;
  color: #292F42;
}

.file-transfer .showfilebox .right span {
  display: inline-block;
  color: var(--green);

  line-height: 30px;
  width: 30px;
  height: 30px;
  text-align: center;
  cursor: pointer;
  font-size: 30px;
  border-radius: 10px;
}

.file-transfer .showfilebox .right span:hover {
  color: var(--green);
}

.file-transfer .upload-file label {
  height: 167px;
  display: flex;
  cursor: pointer;
  margin: 30px 0;
  align-items: center;
  flex-direction: column;
  border: 2px dashed var(--green);
  border-radius: 5px;
  justify-content: center;
}

.file-transfer .upload-file .icon-upload {
  font-size: 50px;
  color: var(--green);
}

.file-transfer .upload-file p {
  margin-top: 15px;
  font-size: 16px;
  text-align: center;
  color: var(--green);
}

.submit-btn {
  width: 100%;
  background: var(--green);
  border: none;
  border-radius: 8px;
  font-size: 16px;
  color: #fff;
  padding: 13px 24px;
  font-weight: 500;
  text-align: center;
  text-transform: capitalize;
  cursor: pointer;
  transition: all 0.4s;
}


.form-excel {
  width: 100%;
}



.container-upload {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 1rem;
  justify-content: center;
}

.container-upload .upload-file{
  width: 100% !important;
}




.file-input {
  visibility: hidden !important;
}




.delete-button-table {
  background-color: var(--green);
  width: 2.3rem;
  height: 2.3rem;
  border-radius: 20%;
  color: white;
  border: none;
  cursor: pointer; 
  font-size: 1.4rem;
   padding-top: 0.3rem;
  /* border: solid 1px rgb(164, 159, 159);  */
}



.container-main-form {
  overflow-y: scroll !important;
  height: 84vh;
}



div:where(.swal2-container) {
  z-index: 9999
}

.css-19midj6 {
  padding: 0 !important;
}

.css-1aquho2-MuiTabs-indicator {
  background-color: var(--green) !important;
}

.css-1h9z7r5-MuiButtonBase-root-MuiTab-root.Mui-selected {
  color: var(--green) !important;
}

.dark-mode .delete-button-table,
.dark-mode .css-1aquho2-MuiTabs-indicator,
.dark-mode .submit-btn {
  background-color: var(--orange);
}

.dark-mode .file-transfer .upload-file p,
.dark-mode .file-transfer .upload-file .icon-upload {
  color: var(--orange);
}

.dark-mode .css-1h9z7r5-MuiButtonBase-root-MuiTab-root.Mui-selected {
  color: var(--orange) !important; 
}

.dark-mode .css-1aquho2-MuiTabs-indicator {
  background-color: var(--orange) !important;
}


.dark-mode .file-transfer .upload-file label {
  border: 2px dashed var(--orange);
}

.dark-mode .file-transfer .showfilebox .left h3 {
  color: white;
}